@import "compass/css3/text-shadow";
@import "compass/css3/border-radius";
html,
body{
  background-color: blue;
  @include single-text-shadow(lighten(#000, 10%), 5px, 5px, 35px);
  font-size: 30px;
  @include border-top-right-radius(30px);
  border: #000 2px solid;
}
.menu {
  list-style: none;
  border-bottom: 0.1em solid black;
  margin-bottom: 2em;
  padding: 0 0 0.5em;
  border-radius: 1px;
  box-shadow: red 1px 2px 2px;
}

.menu:before {
  content: "[";
}

.menu:after {
  content: "]";
}

.menu > li {
  display: inline;
}

.menu > li:before {
  content: "|";
  padding-right: 0.3em;
}

.menu > li:nth-child(1):before {
  content: "";
  padding: 0;
}
